<template>
  <Menu mode="horizontal" theme="dark" active-name="1" :style="{height: '64px'}" @on-select="menuSelect">
    <div class="layout-nav">
      <MenuItem name="1">
        <Icon type="ios-keypad"></Icon>
          Item 2
      </MenuItem>
      <MenuItem name="2">
        <Icon type="ios-analytics"></Icon>
          Item 3
      </MenuItem>
      <MenuItem name="3">
        <Icon type="ios-paper"></Icon>
          Item 4
      </MenuItem>
      <MenuItem name="4">
        <Icon type="md-power"></Icon>
          退出
      </MenuItem>
    </div>
  </Menu>
</template>

<script>
export default {
  name: 'theader',
  data () {
    return {

    }
  },
  computed: {
    username: function(){
      return this.$store.getters.getLoginInfo;
    }
  },
  methods: {
    menuSelect(name) {
      console.log(name);
      switch(name) {
        case "1":
        break;
        case "2":
        break;
        case "3":
        break;
        case "4":
          this.$router.push("/");
        break;
      }
    }
  }
}
</script>
<style scoped>
.layout-nav{
  width: 420px;
  margin: 0 auto;
  margin-right: 20px;
}
</style>


