import Mock from 'mockjs';
const Random = Mock.Random;
/*模拟登陆数据*/
const loginData = function() {
  let loginList = [];
  for (let i = 0; i < 10; i++) {
    let loginDataObject = {
      usernanme: Random.csentence(5,30),
      password: Random.date() + '' + Random.time()
    }
    loginList.push(loginDataObject);
  }
  return {
    loginList: loginList
  }
}

Mock.mock('/login','get',loginData);

/*模拟表格操作生成数据*/
const tableData = function(options) {
  let tableDataList = [];
  for (let i = 0; i < 50; i++) {
    let tableDataObject = {
      id: i,
      username: Random.cname(4),
      shenpistatus: Random.ctitle(5),
      shenpicomments: Random.ctitle(5),
      time: Random.date() + ' ' + Random.time()
    }
    tableDataList.push(tableDataObject);
  }
  return {
    tableDataList: tableDataList
  }
}
Mock.mock('/table','get',tableData);

/*模拟表格操作删除数据*/
const deleteTableData = function(options) {
  tableDataList = tableDataList.filter(function(val){
    return val.id != id;
  })
  return {
    tableDataList: tableDataList
  }
}
Mock.mock('/table/delete','get',deleteTableData);

/*模拟时间轴数据*/
const timelineData = function() {
  let timelineDataList = [];
  for (let i = 0; i < 6; i++) {
    let timelineObject = {
      id: i,
      time: Random.date(),
      content: Random.ctitle(5) + '随机数据'
    }
    timelineDataList.push(timelineObject);
  }
  return {
    timelineDataList: timelineDataList
  }
}
Mock.mock('/timeline','get',timelineData);

/*模拟无限滚动数据*/
const allScrollData = function() {
  let allScrollDataList = [];
  for (let i = 0; i< 20; i++) {
    let allScrollObject = {
      id: i,
      title: Random.ctitle(5),
      content: Random.csentence(5,30),
      time: Random.date()
    }
    allScrollDataList.push(allScrollObject);
  }
  return {
    allScrollDataList: allScrollDataList
  }
}
Mock.mock('/allScroll','get',allScrollData);

/*模拟折叠面板数据*/
const collapseData = function() {
  let collapseDataList = [];
  for (let i = 0; i< 5; i++) {
    let collapseObject = {
      name: i+1+'',
      username: Random.cname(5),
      content: Random.csentence(500)
    }
    collapseDataList.push(collapseObject);
  }
  return {
    collapseDataList: collapseDataList
  }
}
Mock.mock('/collapse','get',collapseData);



