<template>
  <Drawer :closable="false" width="300" v-model="isOpen" :on-close="closedDrawer">
    <p :style="pStyle">个人详情</p>
    <Divider />
    <div class="demo-drawer-profile">
      <Row class="profile-line">
        人员: {{personalData.username}}
      </Row>
      <Row class="profile-line">
        操作: {{personalData.shenpistatus}}
      </Row>
      <Row class="profile-line">
        意见: {{personalData.shenpicomments}}
      </Row>
      <Row class="profile-line">
        时间: {{personalData.time}}
      </Row>
    </div>
  </Drawer>
</template>
<script>
  export default {
    name: 'hdrawer',
    props: {
      personalData: Object
    },
    data () {
      return {
        pStyle: {
          fontSize: '16px',
          color: 'rgba(0,0,0,0.85)',
          lineHeight: '24px',
          display: 'block',
          marginBottom: '16px'
        }
      }
    },
    computed: {
      isOpen: {
        get: function() {
          return this.$store.getters.getDrawerOpen;
        },
        set: function() {
          this.closedDrawer();
        }
      }
    },
    methods: {
      closedDrawer() {
        this.$store.dispatch("setDrawerOpen", false);
      }
    }
  }
</script>
<style>
  .demo-drawer-profile{
    font-size: 14px;
  }
  .demo-drawer-profile .ivu-col{
    margin-bottom: 24px;
  }
  .demo-drawer-profile .profile-line {
    margin-bottom: 24px;
  }
</style>



