<template>
  <div class="demo-split">
    <Split v-model="split1">
      <div slot="left" class="demo-split-pane" style="text-align: left">
        <ttree></ttree>
      </div>
      <div slot="right" class="demo-split-pane">
        Right Pane
      </div>
    </Split>
  </div>
</template>
<script>
  import ttree from '@/components/component/tree/index'
  export default {
    name: 'test7',
    components: {
      ttree: ttree
    },
    data () {
      return {
        split1: 0.2
      }
    }
  }
</script>
<style>
  .demo-split{
    height: 400px;
    border: 1px solid #dcdee2;
  }
  .demo-split-pane{
    padding: 10px;
  }
</style>


