<template>
  <Collapse accordion v-model="value3">
    <Panel  v-for="item in collapseDataList" :key="item.name" :name="item.name">
      {{ item.username }}
      <div slot="content">
        {{ item.content }}
      </div>
    </Panel>
  </Collapse>
</template>
<script>
  export default {
    name: 'tcollapse',
    data () {
      return {
        collapseDataList: [],
        value3:'1'
      }
    },
    methods: {
      getAjaxData() {
        this.$axios.get('/collapse').then((response) => {
          this.collapseDataList = response.data.collapseDataList;
        }).catch((error) => {
          console.log(error);
        })
      }
    },
    created() {
      this.getAjaxData();
    }
  }
</script>



