<template>
  <Timeline>
    <TimelineItem v-for="item in timelineDataList" :key="item.id">
      <p class="time">{{item.time}}</p>
      <p class="content">{{item.content}}</p>
    </TimelineItem>
  </Timeline>
</template>
<script>
  export default {
    name: 'ttimeline',
    data() {
      return {
        timelineDataList: []
      }
    },
    methods: {
      getData() {
        this.$axios.get('/timeline').then((response) => {
          this.timelineDataList = response.data.timelineDataList;
        }).catch((error) => {
          console.log(error);
        });
      }
    },
    created() {
      this.getData();
    }
  }
</script>
<style scoped>
  .time{
    font-size: 14px;
    font-weight: bold;
  }
  .content{
    padding-left: 5px;
  }
</style>



