        <Input v-model="userpwd" placeholder="密码" style="width: 300px" />
      </div>
      <Row style="margin-top: 40px">
        <Col span="12">
          <Button type="success" long @click="login" style="width: 140px">登陆</Button>
        </Col>
        <Col span="12">
          <Button type="warning" style="width: 140px">重置</Button>
        </Col>
      </Row>
    </div>
  </div>
</template>

