export default {
  name: 'login',
  data () {
    return {
      username: '',
      userpwd: ''
    }
  },
  methods: {
    login: function(){
      this.$store.dispatch("setLoginInfo", this.username);
      this.$router.push("/home");
    }
  },
  created() {
    this.$axios.get('/login').then((response) => {
      
    });
  }
}
</script>

