<script>
  export default {
    name: 'home',
    data() {
      return {
        postData: {
          pageNo: 1,
          pageSize: 6
        },
        bookList: [],
        ajaxBookList: [],
        dataCount: 0,
        // 每页显示多少条
        pageSize: 60,
        // 搜索关键字
        searchInput: '',
        //网络错误
        netWorkError: false
      }
    },
    watch: {
      searchInput: function() {
        if (this.searchInput == '') {
          this.getAjax();
        } else {
          this.getAjaxByParams(this.searchInput);
        }
      }
    },
    computed: {

    },
    mounted() {
      this.getAjax();
    },
    methods: {
      getAjax: function() {
        this.$axios.get('/api/rest/xuhuang/bookstore/v1/book')
        .then((response) => {
          this.ajaxBookList = response.data.detail;
          console.log(this.ajaxBookList);
          this.dataCount = this.ajaxBookList.length;
          if (this.dataCount > this.pageSize) {
            this.bookList = this.ajaxBookList.slice(0,this.pageSize);
          } else {
            this.bookList = this.ajaxBookList;
          }
        })
        .catch((error) => {
          console.log(error);
          this.netWorkError = true;
        });
      },
      getAjaxByParams: function(name,type,adaptAge) {
        this.$axios.get('/api/rest/xuhuang/bookstore/v1/book',{
          params: {
            name: name,
            type: type,
            adaptAge: adaptAge
          }
        })
        .then((response) => {
          this.ajaxBookList = response.data.detail;
          this.dataCount = this.ajaxBookList.length;
          if (this.dataCount > this.pageSize) {
            this.bookList = this.ajaxBookList.slice(0,this.pageSize);
          } else {
            this.bookList = this.ajaxBookList;
          }
        })
        .catch((error) => {
          console.log(error);
        });
      },
      changepage: function (index) {
        var _start = (index - 1) * this.pageSize;
        var _end = index * this.pageSize;
        this.bookList = this.ajaxBookList.slice(_start,_end);
      },
      searchBookByName: function (input) {
        if (input) {
          this.getAjaxByParams(input,input,'');
        } else {
          this.getAjax();
        }
      },
      searchBookByAge: function (adaptAge) {
        if (adaptAge == 'all') {
          this.getAjax();
        } else {
          this.getAjaxByParams('','',adaptAge);
        }
      },
      briefMessage: function (message) {
        this.$Message.info({
          content: message,
          duration: 0,
          closable: true
        });
      },
      bookNameMessage: function (message) {
        this.$Message.info({
          content: message,
          duration: 0,
          closable: true
        });
      },
      bookAuthorMessage: function (message) {
        this.$Message.info({
          content: message,
          duration: 10,
          closable: true
        });
      }
    }
  }
</script>
