<template>
  <div>
    <Table :columns="historyColumns" :data="historyData"></Table>
    <Page :total="dataCount" :page-size="pageSize" show-total class="paging" @on-change="changepage"></Page>
    <hdrawer :personalData=personalData></hdrawer>
  </div>
</template>
<style scoped>
  .paging{
    float:right;
    margin-top:10px;
  }
</style>
<script>
  import hdrawer from '@/components/component/drawer/index'
  export default {
    name: 'test1',
    components: {
      hdrawer: hdrawer
    },
    data () {
      return {
        personalData: {},
        ajaxHistoryData:[],
        // 初始化信息总条数
        dataCount:0,
        // 每页显示多少条
        pageSize:5,
        historyColumns: [
          {
            type: 'index',
            width: 60,
            align: 'center'
          },
          {
            title: '人员',
            key: 'username'
          },
          {
            title: '操作',
            key: 'shenpistatus'
          },
          {
            title: '意见',
            key: 'shenpicomments'
          },
          {
            title: '时间',
            key: 'time'
          },
          {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h,params) => {
              return h('div',[
                h('Button',{
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params);
                    }
                  }
                },'View'),
                h('Button',{
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.id)
                    }
                  }
                },'Delete')
              ]);
            }
          }
        ],
        historyData: []
      }
    },
    methods:{
      // 获取历史记录信息
      handleListApproveHistory(){
        this.$axios.get('/table').then((response) => {
          // 保存取到的所有数据
          this.ajaxHistoryData = response.data.tableDataList;
          this.dataCount = this.ajaxHistoryData.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.ajaxHistoryData.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
        })
      },
      changepage(index){
        var _start = ( index - 1 ) * this.pageSize;
        var _end = index * this.pageSize;
        this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      //表格点击view事件
      show(params){
        this.$store.dispatch("setDrawerOpen", true);
        this.personalData = params.row;
      },
      remove(index){
        this.$Modal.confirm({
          title: '删除提示',
          content: '<p>确定删除人员</p>',
          onOk: () => {
            this.$Message.info('删除成功');
            this.ajaxHistoryData = this.ajaxHistoryData.filter(function(val){
              return val.id != index;
            })
            this.dataCount = this.ajaxHistoryData.length;
            // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
            if(this.ajaxHistoryData.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
            }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
            }
          },
          onCancel: () => {
            //this.$Message.info('Clicked cancel');
          }
        });
      }
    },
    created(){
      this.handleListApproveHistory();
    }
  }
</script>



