<template>
  <Menu :theme="themed" active-name="1-2" width="auto" :open-names="[menuData[0].name]" @on-select="menuSelect" :class="menuitemClasses" :accordion="true">
    <Submenu v-for="item in menuData" :name="item.name" :key="item.name">
      <template slot="title">  
        <Icon :type="item.icon"></Icon>
        <span>{{item.label}}</span>  
      </template>
      <menu-item v-for="mi in item.menuitems" :name="mi.name" :key="mi.name">
        <Icon :type="item.icon"></Icon>
        <span>{{mi.label}}</span>
      </menu-item>  
    </Submenu>
  </Menu>
</template>

<script>
import config from '@/components/config/config.js' 
export default {
  name: 'leftmenus',
  data () {
    return {
      themed: 'dark',
      menuData: config.leftMenusConfig,
      breadCrumbs: []
    }
  },
  computed: {
    menuitemClasses () {
      return [
        'menu-item',
        this.$store.getters.getCollapsedSider ? 'collapsed-menu' : ''
      ]
    }
  },
  methods: {
    menuSelect: function(name){
      this.$router.push(name);
    }
  },
  watch: {
    '$route' (newRoute) {
      this.$store.dispatch("setBreadCrumbs",newRoute.matched);
    }
  }
}
</script>

<style scoped>
  .menu-item span{
    display: inline-block;
    overflow: hidden;
    width: 69px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: bottom;
    transition: width .2s ease .2s;
  }
  .menu-item i{
    transform: translateX(0px);
    transition: font-size .2s ease, transform .2s ease;
    vertical-align: middle;
    font-size: 16px;
  }
  .collapsed-menu span{
    width: 0px;
    transition: width .2s ease;
  }
  .collapsed-menu i{
    transform: translateX(5px);
    transition: font-size .2s ease .2s, transform .2s ease .2s;
    vertical-align: middle;
    font-size: 22px;
  }
</style>



