import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

var store = new Vuex.Store({
  state: {
    loginInfo: '',
    breadCrumbsList: 'home',
    isCollapsed: false,
    isOpen: false
  },

  getters: {
    getLoginInfo(state){
      return state.loginInfo;
    },
    getBreadCrumbs(state){
      return state.breadCrumbsList;
    },
    getCollapsedSider(state){
      return state.isCollapsed;
    },
    getDrawerOpen(state){
      return state.isOpen;
    }
  },

  actions: {
    setLoginInfo({commit,state},name){
      commit('setLoginInfo',name);
    },
    setBreadCrumbs({commit,state},name){
      commit('setBreadCrumbs',name);
    },
    setCollapsedSider({commit,state},name){
      commit('setCollapsedSider',name);
    },
    setDrawerOpen({commit,state},name){
      commit('setDrawerOpen',name);
    }
  },

  mutations: {
    setLoginInfo(state,name){
      state.loginInfo = name;
    },
    setBreadCrumbs(state,name){
      state.breadCrumbsList = name;
    },
    setCollapsedSider(state,name){
      state.isCollapsed = name;
    },
    setDrawerOpen(state,name){
      state.isOpen = name;
    }
  }
});

export default store;


