<template>
  <Alert banner show-icon :type="alertShow.type">
    <template slot="desc">
      {{ alertShow.content }}
    </template>
  </Alert>
</template>

<script>
  export default {
    name: 'talert',
    props: {
      alertShow: Object
    }
  }
</script>


