<template>
  <Tree :data="treedata" :render="renderContent" @on-select-change="nodeClick"></Tree>
</template>
<script>
import config from '@/components/config/config.js' 
export default {
  name: 'ttree',
  data () {
    return {
      treedata: [
        {
          title: config.treeConfig.title,
          expand: config.treeConfig.expand,
          render: (h, { root, node, data}) => {
            return h('span', {
              style: {
                display: 'inline-block',
                width: '100%',
                cursor: 'pointer',
                fontSize: '14px'
              }
            },[
              h('span', [
                h('Icon', {
                  props: {
                    type: 'ios-folder-outline'
                  },
                  style: {
                    marginRight: '8px'
                  }
                }),
                h('span', data.title)
              ]),
              h('span', {
                style: {
                  display: 'inline-block',
                  float: 'right',
                  marginRight: '32px',
                  fontSize: '14px'
                }
              })
            ]);
          },
          children: config.treeConfig.children
        }
      ]
    }
  },
  methods: {
    renderContent (h, { root, node, data}) {
      return h('span', {
        style: {
          display: 'inline-block',
          width: '100%',
          cursor: 'pointer',
          fontSize: '14px'
        }
      },[
        h('span', [
          h('Icon', {
            props: {
              type: 'ios-folder-outline'
            },
            style: {
              marginRight: '8px'
            }
          }),
          h('span', data.title)
        ]),
        h('span', {
          style: {
            display: 'inline-block',
            float: 'right',
            marginRight: '32px',
            fontSize: '14px'
          }
        })
      ]); 
    },
    nodeClick () {
      console.log("123");
    }
  }
}
</script>
<style scoped>
  .ivu-tree ul li {
    margin: 20px 0px !important;
  }
</style>


