<template>
  <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
    <FormItem label="姓名" prop="name">
      <Input v-model="formValidate.name" placeholder="输入姓名"></Input>
    </FormItem>
    <FormItem label="邮件" prop="mail">
      <Input v-model="formValidate.mail" placeholder="输入邮件"></Input>
    </FormItem>
    <FormItem label="城市" prop="city">
      <Select v-model="formValidate.city" placeholder="选择你的城市">
        <Option value="beijing">北京</Option>
        <Option value="shanghai">天津</Option>
        <Option value="shenzhen">上海</Option>
      </Select>
    </FormItem>
    <FormItem label="日期">
      <Row>
        <Col span="11">
          <FormItem prop="date">
            <DatePicker type="date" placeholder="选择日期" v-model="formValidate.date"></DatePicker>
          </FormItem>
        </Col>
        <Col span="2" style="text-align: center">-</Col>
        <Col span="11">
          <FormItem prop="time">
            <TimePicker type="time" placeholder="选择时间" v-model="formValidate.time"></TimePicker>
          </FormItem>
        </Col>
      </Row>
    </FormItem>
    <FormItem label="性别" prop="gender">
      <RadioGroup v-model="formValidate.gender">
        <Radio label="male">男</Radio>
        <Radio label="female">女</Radio>
      </RadioGroup>
    </FormItem>
    <FormItem label="爱好" prop="interest">
      <CheckboxGroup v-model="formValidate.interest">
        <Checkbox label="音乐"></Checkbox>
        <Checkbox label="运动"></Checkbox>
        <Checkbox label="电影"></Checkbox>
        <Checkbox label="阅读"></Checkbox>
      </CheckboxGroup>
    </FormItem>
    <FormItem label="描述" prop="desc">
      <Input v-model="formValidate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请留下点什么..."></Input>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit('formValidate')">提交</Button>
      <Button @click="handleReset('formValidate')" style="margin-left: 8px">重置</Button>
    </FormItem>
  </Form>
</template>
<script>
export default {
  data () {
    var validateDesc = function (rule, value, callback) {
      if (value === '') {
        callback(new Error('请输入描述'));
      } else if (!/^[a-zA-Z\u4e00-\u9fa5]+$/.test(value)) {
        callback(new Error('请输入汉字或字符'));
      } else {
        callback();
      }
    };
    return {
      formValidate: {
        name: '',
        mail: '',
        city: '',
        gender: '',
        interest: [],
        date: '',
        time: '',
        desc: ''
      },
      ruleValidate: {
        name: [
          { required: true, message: '姓名不能为空', trigger: 'blur' },
          { pattern: /^[a-zA-Z\u4e00-\u9fa5]+$/, message: '汉字或字符', trigger: 'blur' }
        ],
        mail: [
          { required: true, message: '邮箱不能为空', trigger: 'blur' },
          { type: 'email', message: '邮箱格式不正确', trigger: 'blur' }
        ],
        city: [
          { required: true, message: '请选择城市', trigger: 'change' }
        ],
        gender: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ],
        interest: [
          { required: true, type: 'array', min: 1, message: '至少选择一个爱好', trigger: 'change' },
          { type: 'array', max: 2, message: '最多选择两个爱好', trigger: 'change' }
        ],
        date: [
          { required: true, type: 'date', message: '请选择日期', trigger: 'change' }
        ],
        time: [
          { required: true, type: 'string', message: '请选择时间', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '请填写个人简介', trigger: 'blur' },
          { type: 'string', min: 20, message: '个人简介至少20个字', trigger: 'blur' }, 
          { validator: validateDesc, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success('提交成功!');
          console.log(this.formValidate);
        } else {
          this.$Message.error('Fail!');
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    }
  }
}
</script>



