<template>
  <div class="layout">
    <Layout>
      <Sider ref="side1" hide-trigger collapsible :collapsed-width="78" v-model="isCollapsed" :isCollapsed="isCollapsed" :style="{height: '100vh'}">
        <Layout>
          <Header :style="{display: 'flex', justifyContent: 'center', padding: '0px', borderBottom: '1px solid #F5F7F9', borderRight: '1px solid #F5F7F9'}">
            <div class="layout-logo">
              <img :src="logoUrl">
            </div>
          </Header>
          <Content>
            <leftmenus>
            </leftmenus>
          </Content>
        </Layout>
      </Sider>
      <Layout>
        <Header :style="{padding: 0}" class="layout-header-bar">
          <Row>
            <Col span="2" :style="{background: '#515a6e'}">
              <Icon @click.native="collapsedSider" :class="rotateIcon" :style="{color: '#FFF', marginLeft: '20px '}" type="md-menu" size="24"></Icon>
            </Col>
            <Col span="22">
              <theader>
              </theader>
            </Col>
          </Row>
        </Header>
        <Content :style="{margin: '20px', background: '#fff', minHeight: '260px', padding: '24px'}">
          <router-view/>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>
<script>
  import config from '@/components/config/config.js'
  import theader from '@/view/mainpage/theader/theader'
  import leftmenus from '@/view/mainpage/leftmenus/leftmenus'
  import logoUrl from '@/assets/logo.png'
  export default {
    name: 'mainpage',
    data () {
      return {
        logoUrl: logoUrl,
        isCollapsed: false,
        themed: 'dark',
        menuData: config.leftMenusConfig
      }
    },
    components: {
      theader: theader,
      leftmenus: leftmenus
    },
    computed: {
      rotateIcon () {
        return [
          'menu-icon',
          this.isCollapsed ? 'rotate-icon' : ''
        ];
      }
    },
    methods: {
      collapsedSider () {
        this.$refs.side1.toggleCollapse();
        this.$store.dispatch("setCollapsedSider", this.isCollapsed);
      }
    }
  }
</script>
<style scoped>
  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-header-bar {
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
    text-align: left;
  }
  .layout-header-bar::-webkit-scrollbar {
    width: 10px;
    height: 1px;
  }
  .layout-header-bar::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: #535353;
  }
  .layout-header-bar::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 10px;
    background: #EDEDED;
  }
  .layout-logo-left {
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  .menu-icon {
    transition: all .3s;
  }
  .rotate-icon {
    transform: rotate(-90deg);
  }
  .layout-logo {
    height: 64px;
    width: 64px;
  }
  .layout-logo img {
    height: 64px;
    width: 64px;
  }
</style>


